* {
  box-sizing: border-box;
}

body {
  font-family: "Karla", sans-serif;
  margin: 0;
}

main {
  padding: 2.25rem;
}

/* Header */
.header {
  display: flex;
  align-items: center;
  height: 4rem;
  background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
  color: white;
  padding: 1.25rem;
}

.header--image {
  height: 100%;
  margin-right: 0.375rem;
}

.header--title {
  font-size: 1.25rem;
  margin-right: auto;
}

.header--project {
  font-size: 0.75rem;
  font-weight: 500;
}

/* Form */
.form {
  display: grid;
  grid-template: 40px 40px / 1fr 1fr;
  gap: 1.0625rem;
  margin-bottom: 1.0625rem;
}

.form--input {
  font-family: inherit;
  border-radius: .3125rem;
  border: 1px solid #D5D4D8;
  text-indent: 5px;
}

.form--button {
  grid-column: 1 / -1;
  font-family: inherit;
  border-radius: 5px;
  background: linear-gradient(90.41deg, #711F8D 1.14%, #A818DA 100%);
  color: white;
  border: none;
  cursor: pointer;
}

.meme {
  position: relative;
  background-color: red;
  max-width: 50%;
  margin: 0 auto;
  word-break: break-all;
}

.meme--image {
  display: block;
  width: 100%;
  border-radius: 3px;
  margin: 0 auto;

}

.meme--text {
  position: absolute;
  /* max-width: 50%; */
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  margin: .9375rem 0;
  padding: 0 .3125rem;
  font-family: impact, sans-serif;
  font-size: 2vw;
  text-transform: uppercase;
  color: white;
  letter-spacing: 1px;
  
  text-shadow:
    2px 2px 0 #000,
    -2px -2px 0 #000,
    2px -2px 0 #000,
    -2px 2px 0 #000,
    0 2px 0 #000,
    2px 0 0 #000,
    0 -2px 0 #000,
    -2px 0 0 #000,
    2px 2px 5px #000;
}

.bottom {
  bottom: 0;
}

.top {
  top: 0;
}